@import "_variables";

.contact-form {
  box-sizing: border-box;
  border: solid 1px rgba(255, 255, 255, 0.281);
  background-color: rgba(255, 255, 255, 0.062);
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  padding: 20px 30px;
  margin: 2rem;
  color: #fff;
  min-width: min(90vw, 600px);
  a {
    color: white;
    text-decoration: underline;
    margin-bottom: 0.3rem;
  }
}

.contact-header > h2 {
  font-size: clamp(1.7rem, 4vw, 2.4rem);
  text-align: left;
  color: inherit;
}

.contact-form > .field {
  border: none;
  margin-bottom: 10px;
}

.contact-form > .field > .contact-input,
.PhoneInputInput {
  box-sizing: border-box;
  border-radius: 5px;
  border: rgba(0, 0, 0, 0.651) solid 1px;
  background-color: rgba(255, 255, 255, 0.164);
  width: 25rem;
  color: rgb(255, 255, 255);
  font-size: 1.5rem;
  font-family: inherit;
  transition: all ease 0.3s;
  width: 100%;
  padding: 0.5rem 1rem;
}
.contact-input::selection {
  background: rgba(255, 255, 255, 0.226);
}
.contact-form > .contact-input.button {
  border: none;
  outline: none;
  width: 50%;
  align-self: center;
  background-color: rgb(51, 114, 165);
  color: rgb(255, 255, 255);
  font: inherit;
  font-size: 1.5rem;
  padding: 0.5rem;
  border-radius: 0.2rem;
  cursor: pointer;
  transition: 0.2s;
}

.contact-form > .contact-input.button:disabled {
  background-color: rgb(83, 83, 83);
}

.contact-form > .contact-input.button:hover {
  background-color: rgb(72, 152, 218);
}

.contact-form > .field > .contact-input.area {
  resize: none;
  height: 10rem;
}

.contact-form > .field > .contact-input:hover,
.PhoneInputInput:hover {
  border: rgba(130, 226, 255, 0.658) solid 1px;
  box-shadow: 0 0 10px rgba(130, 226, 255, 0.658);
}

.contact-form > .field > .contact-input:focus,
.PhoneInputInput:focus {
  outline: none;
  border: rgba(137, 218, 255, 0.589) solid 1px;
  box-shadow: none;
}

.PhoneInputCountryFlag-height {
  width: 100px;
}

p.error {
  color: rgb(240, 121, 11);
  font-size: 1.2rem;
}

textarea {
  resize: none;
}

@media only screen and (max-width: $mobile-width) {
  .contact-form {
    .field {
      .contact-input {
        font-size: 1rem;
      }
    }
  }
}
